import { Card, Button, ListGroup, Image } from "react-bootstrap";

import { useTranslation } from "react-i18next";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck } from "@fortawesome/free-solid-svg-icons";

import styles from "./styles.module.scss";
interface MembershipCardProps {
  image: string;
  imageName: string;
  type: string;
  period: string;
  price: string;
  currency: string;
  features: string[];
  join: string
}

export default function MembershipCard({
  image,
  imageName,
  type,
  period,
  price,
  currency,
  features,
  join
}: MembershipCardProps) {
  const { t } = useTranslation();
  return (
    <Card className={`${styles.card} ms-3 rounded p-0 mb-5 flex-fill rounded-3`} >
      <Card.Header className={`bg-primary bg-opacity-10 rounded-top border-0`}>
        <div className="d-flex align-items-center justify-content-between">
          <h3 className="text-blue"> {t(`${type}`)} </h3>
          <small className="text-blue opacity-40">{t(`${period}`)} </small>
        </div>
        <div className={`${styles.cardText}`}>
          {t(`${price}`)}
          <span className="fs-8 align-super mt-1">{t(`${currency}`)} </span>
        </div>
      </Card.Header>

      <Card.Body>
        <div className="text-center">
          <Image src={image} alt={imageName} className='p-4' width={182} height={182} />
        </div>

        <ListGroup className={`${styles.list} list-group-flush`}>
          {features.map((item, index) => (
            <ListGroup.Item key={index} className={styles.listItem}>
              <span className="mx-3"> <FontAwesomeIcon icon={faCheck} /></span>
              {t(`${item}`)}
            </ListGroup.Item>
          ))}
        </ListGroup>
      </Card.Body>
      <Button className={`py-2  m-4 ${styles.join}`}>
        {t(`${join}`)}
      </Button>
    </Card>
  );
}
