import React from "react";
import { Container, Row, Card, Button, Col } from "react-bootstrap";
import MembershipCard from "../MembershipCard";
import { useTranslation } from "react-i18next";
import Image1 from "../../assets/images/student-membership.png";
import Image2 from "../../assets/images/Affiliate-membership.png";
import Image3 from "../../assets/images/worker-membership.png";
import styles from "./styles.module.scss";
export default function MembershipList() {
  const { t } = useTranslation();
  const list = [
    {
      image: Image1,
      imageName: "student-membership",
      type: "Memberships.card2.type",
      period: "Memberships.card2.period",
      price: "Memberships.card2.price",
      currency: "",
      features: [
        "Memberships.card2.features.feature1",
        "Memberships.card2.features.feature2",
        "Memberships.card2.features.feature3",
      ],
      join: "Memberships.card2.join",
    },
    {
      image: Image2,
      imageName: "Affiliate-membership",
      type: "Memberships.card3.type",
      period: "Memberships.card3.period",
      price: "Memberships.card3.price",
      currency: "Memberships.card3.currency",
      features: [
        "Memberships.card3.features.feature1",
        "Memberships.card3.features.feature2",
        "Memberships.card3.features.feature3",
        "Memberships.card3.features.feature4",
      ],
      join: "Memberships.card3.join",
    },
    {
      image: Image3,
      imageName: "worker-membership",
      type: "Memberships.card4.type",
      period: "Memberships.card4.period",
      price: "Memberships.card4.price",
      currency: "Memberships.card4.currency",
      features: [
        "Memberships.card4.features.feature1",
        "Memberships.card4.features.feature2",
        "Memberships.card4.features.feature3",
        "Memberships.card4.features.feature4",
        "Memberships.card4.features.feature5",
        "Memberships.card4.features.feature6",
      ],

      join: "Memberships.card4.join",
    },
  ];

  return (
    <Container fluid >
      <Row className="mt-5">
        <Col md={2}>
          <Card className={`${styles.card} mb-5`}>
            <Card.Body>
              <Card.Title className='fs-3'>
                {t("Memberships.card1.title")}
                <h1> {t("Memberships.card1.subTitle")}</h1>
              </Card.Title>
              <Card.Text>{t("Memberships.card1.description")}</Card.Text>
              <Button variant="primary" className={`py-2 my-3 w-100  ${styles.createAccount}`}>
                {t("Memberships.card1.createAccount")}
              </Button>
            </Card.Body>
          </Card>
        </Col>
        <Col md={10}>
          <div className="d-md-flex">
            {list.map((membership, index) => (
              <MembershipCard {...membership} key={index} />
            ))}
          </div>
        </Col>

      </Row>
    </Container>
  );
}
